/* admin_home_page_header */

#admin_home_page_header{
	height: 13vh;
	width: 100%;
	position: sticky;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0,0.95);
	color: var(--fourth-color);
	z-index: 3;
}

